export default {
	data() {
		return {
			password:'',
			surepassword:'',
			sessionId:''
		}
	},
	onLoad(option) {
		this.sessionId = option.sessionId
	},
	methods: {
		// 提交
		sure(){
			if(this.password != this.surepassword){
				uni.showToast({
					title:'密码不一致',
					icon:'none'
				})
			}else{
				uni.request({
					url:'https://www.nssnail.com/wx/personal/setUserPayPwd',
					method:'POST',
					data:{
						payPwd :this.password
					},
					success: (res) => {
						if(res.data.stat == 1){
							uni.showToast({
								title:'设置成功',
								icon:'success',
								success:()=>{
									setTimeout(function(){
										uni.navigateTo({
											url:'../information/information'
										})
									},2000);
								}
							})
						}else if(res.data.stat == 3){
							uni.showModal({
							    title: '提示',
							    content: '登录过期，请重新登录',
							    success: function (res) {
							        if (res.confirm) {
							            uni.navigateTo({
							            	url:'../myInit/myInit'
							            })
							        }
							    }
							})
						}
					}
				})
			}
		}
	}
}
